<template>
    <div class="login">
        <div class="logo">
            <img src="@/assets/img/logo_01.png" alt="">
            <img src="@/assets/img/logo_02.png" alt="">
        </div>
        <div class="wrap">
            <div class="title">
                <h1>汽车座椅舒适性乘员感知评价系统</h1>
            </div>
            <div class="content">
                <div>
                    <!-- 登录 -->
                    <div class="item code" v-if="STATUS == 'login'">
                        <i class="el-icon-mobile-phone"></i>
                        <input type="text" v-model="account" placeholder="请输入账号，手机号">
                    </div>
                    <div class="item"  v-if="STATUS == 'login'">
                        <i class="el-icon-unlock"></i>
                        <input type="password" v-model="password" placeholder="请输入密码">
                    </div>

                    <!-- 注册 -->
                    <div class="item" v-if="STATUS == 'singup'">
                        <i class="el-icon-truck"></i>
                        <input type="text" v-model="company_name" placeholder="请输入厂商名称">
                    </div>
                    <div class="item code" v-if="STATUS == 'singup'">
                        <i class="el-icon-mobile-phone"></i>
                        <input type="text" v-model="mobile" placeholder="请输入手机号">
                        <p class="sendCode" @click="sendCode">{{CODE_STATUS}}</p>
                    </div>
                    <div class="item"  v-if="STATUS == 'singup'">
                        <i class="el-icon-unlock"></i>
                        <input type="text" v-model="password" placeholder="请输入密码">
                    </div>
                    <div class="item" v-if="STATUS == 'singup'">
                        <i class="el-icon-bank-card"></i>
                        <input type="text" v-model="code" placeholder="请输入验证码">
                    </div>

                    <!-- 授权 -->
                    <div class="item" v-if="STATUS == 'code'">
                        <i class="el-icon-more"></i>
                        <input type="text" placeholder="请输入授权码">
                    </div>

                    <div class="btn-wrap">
                        <div @click="singIn" v-if="STATUS == 'login'">登录</div>
                        <div @click="singUp" v-if="STATUS == 'singup'">注册</div>
                        <div @click="singUp" v-if="STATUS == 'code'">匿名登录</div>
                    </div>
                    

                    <div class="singup">
                        <span type="primary" v-if="STATUS == 'singup' || STATUS == 'code'" @click="STATUS = 'login'" >登录</span>
                        <span type="primary" v-if="STATUS == 'login' || STATUS == 'code'" @click="STATUS = 'singup'" >注册</span>
                        <span type="primary" v-if="STATUS != 'code'" @click="STATUS = 'code'" >匿名登录</span>
                    </div>
                </div>
            </div>
            <div class="footer">
                <p class="z">中汽研 ( 天津 ) 汽车工程研究院有限公司</p>
                <p>CATARC(TIANJIN)Autamotive Enginnering Research Institute Co.,Ltd</p>
            </div>
        </div>
    </div>
</template>

<script>
import api from '../api/index.js';
import qs from 'qs';
export default {
    name: 'login',
    data() {
        return {
            STATUS: 'login', // login singup code
            CODE_STATUS: '发送',
            company_name: '', // 厂商名称
            mobile: '', // 手机号
            password: '', // 密码
            code: '', // 验证码
            scode: '',
            timer: null,
            disabled: false,
            userInfo: null,
            account: '', // 登录账号 手机号
            yzm: '', // 正确的验证码
        }
    },
    methods: {
        sendCode() {
            if(this.CODE_STATUS != '发送') {
                return;
            }0
            if(this.isPhoneNo(this.mobile)) { 
                this.CODE_STATUS = 60;
                this.disabled = 'disabled'
                this.timer = setInterval(() => {
                    if(this.CODE_STATUS == 1) {
                        this.CODE_STATUS = '发送'
                        this.disabled = false;
                        clearInterval(this.timer);
                    } else {
                        this.CODE_STATUS = this.CODE_STATUS-1;
                    }
                }, 500)
                console.log( this.mobile )
                api.sendCode({
                    mobile: this.mobile,
                    scene: 'register'
                }).then(res => {
                    console.log( res )
                    if(res.data.code == 0) {
                        this.$message.warning(res.data.msg);
                    } else if(res.data.code == 1){
                        this.$message.success(res.data.msg);
                        this.yzm = res.data.data.code;
                    }
                })
            } else {
                this.$message.warning('手机号有错误！');
            }
        },
        codeIn() {
            console.log("匿名登录")
        },
        singIn() {
            console.log("登录")
            api.singIn(qs.stringify({
                account: this.account, // 账号
                password: this.password // 密码
            })).then(res => {
                console.log(res)
                if(res.data.code == 0) {
                    this.$message.warning(res.data.msg);
                } else if(res.data.code == 1) {
                    this.$message.success(res.data.msg);
                    this.userInfo = res.data.data.userinfo;

                    localStorage.setItem('userInfo__xes', JSON.stringify(this.userInfo));
                    this.$router.push({name: 'home'})
                }
            })


        },
        singUp() {
            console.log("注册")
            if(!this.company_name) {
                this.$message.warning('请输入厂商名称！');
                return
            }
            console.log( this.yzm );
            console.log( this.code )
            if(this.yzm != this.code) {
                this.$message.warning('验证码错误！');
                return
            }
            if(!this.password) {
                this.$message.warning('请输入密码！');
                return
            }
            if(this.isPhoneNo(this.mobile)) {
                api.singUp({
                    company_name: this.company_name, // 厂商名称
                    mobile: this.mobile, // 手机号
                    password: this.password, // 密码
                    code: this.code // 	验证码
                }).then(res => {
                    console.log(res);
                    if(res.data.code == 0) {
                        this.$message.warning(res.data.msg);
                    } else if(res.data.code == 1) {
                        this.$message.success(res.data.msg);
                    }
                })
            } else {
                this.$message.warning('手机号有错误！');
            }

        },
        isPhoneNo(phone) {
            var pattern = /^1[345678]\d{9}$/;
            if(!pattern.test(phone)){
                console.log('手机号错误！')
                return false
            }
            return true
        }
    }
        
}
</script>
<style lang="stylus" scoped>
.login
    width 100%
    height 100vh
    background #0D3096
    background-image url('~@/assets/img/login_2.jpg')
    background-size 100% 100%
    background-repeat no-repeat
    .logo
        padding 40px 0 0 40px
        img 
            width 80px
            margin-right 12px
    .wrap
        position absolute
        left 50%
        top 50%
        transform translate(-50%, -50%)
        .title
            h1 
                color #DFDFDF
                text-align center
                padding-bottom 50px
                font-size 42px
        .content
            padding 20px
            display flex
            align-items center
            justify-content center
            background rgba(255,255,255,.3)
            border-radius 8px
            overflow hidden
            border 1px solid #FFF
            width 420px
            margin 0 auto
            .item 
                background #DFEBFF
                display flex
                align-items center
                margin-bottom 12px
                height 36px
                width 400px
                border-radius 4px
                overflow hidden
                i 
                    font-size 22px
                    width 36px
                    text-align center
                    background #FFF
                    height 36px
                    line-height 36px
                input 
                    background #C0DDFF
                    border none
                    outline none
                    height 36px
                    line-height 36px
                    flex 1
                    padding 0 12px
                    box-sizing border-box
                .sendCode
                    padding 0 12px
                    background #FFF
                    height 36px
                    line-height 36px
            .btn-wrap
                display flex
                padding-top 20px
                div 
                    width 100%
                    text-align center
                    background #255E9D
                    height 36px
                    line-height 36px
                    color #FFF
                    border-radius 4px
                    font-size 16px
                    cursor pointer
            .singup
                padding-top 12px
                display flex
                justify-content space-between
                span
                    margin-right  12px
                    color #CCCED0
                    cursor pointer
                    font-size 16px
        .footer
            text-align center
            font-size 16px
            color #DFDFDF
            padding-top 50px
            display flex
            flex-direction column
            align-items center
            .z 
                letter-spacing 9px
                border-bottom 1px solid #DFDFDF
                width 475px
            p 
                padding 8px 0
                white-space nowrap



</style>